import React from "react";
import { useState, useCallback, useEffect } from "react";
import Title from "./investment/Title";
import Divide from "./investment/Divide";
import InvestBody from "./investment/InvestBody";
import "../styles/investment.css";

const useMediaQuery = (width) => {
  const [targetReached, setTargetReached] = useState(false);

  const updateTarget = useCallback((e) => {
    if (e.matches) {
      setTargetReached(true);
    } else {
      setTargetReached(false);
    }
  }, []);

  useEffect(() => {
    const media = window.matchMedia(`(min-width: ${width}px)`);
    media.addEventListener("change", (e) => updateTarget(e));

    // Check on mount (callback is not called until a change occurs)
    if (media.matches) {
      setTargetReached(true);
    }

    return () => media.removeEventListener("change", (e) => updateTarget(e));
  }, [updateTarget, width]);

  return targetReached;
};

const Investment = () => {
  const isBreakpoint = useMediaQuery(1440);
  const display =
    isBreakpoint === true ? (
      <div className="invest-container">
        <Title title="Your investment is secure" />
        <Divide />
        <InvestBody />
      </div>
    ) : (
      <div className="invest-container">
        <Divide />
        <div className="invest-container-sub">
          <Title title="Your investment is secure" />
          <InvestBody />
        </div>
      </div>
    );
  return <div className="invest">{display}</div>;
};

export default Investment;
