import React from "react";
import ViewPolicy from "./ViewPolicy";
import ViewDoc from "./ViewDoc";

const InvestCard = ({ title, bodyContent, viewOption }) => {
  const option =
    viewOption === "View policy" ? (
      <ViewPolicy viewOption="View policy" />
    ) : (
      <ViewDoc viewOption="View documentation" />
    );
  return (
    <div className="invest-card">
      <div className="policy-title">{title}</div>
      <p className="policy-content">{bodyContent}</p>
      {option}
    </div>
  );
};

export default InvestCard;
